import { Injectable, HostListener } from '@angular/core';
import {BehaviorSubject, Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import {Product} from '../classes/product';
import {HttpClient} from '@angular/common/http';
import {response} from 'express';

// Menu
export interface Menu {
 path?: string;
 title?: string;
 type?: string;
 megaMenu?: boolean;
 image?: string;
 active?: boolean;
 badge?: boolean;
 badgeText?: string;
 children?: Menu[];
}

@Injectable({
 providedIn: 'root'
})

export class NavService {
    items: BehaviorSubject<Menu[]>;
    leftMenuItems: BehaviorSubject<Menu[]>;

    public MENUITEMS;

    public LEFTMENUITEMS;

    // ===created a data base for the Menus=====

    constructor(private http: HttpClient) {
        this.getMenu.subscribe(resp => this.MENUITEMS = (resp));
        this.getLeftMenu.subscribe(resp => this.LEFTMENUITEMS = (resp));

        // Array
        this.items = new BehaviorSubject<Menu[]>(this.MENUITEMS);
        this.leftMenuItems = new BehaviorSubject<Menu[]>(this.LEFTMENUITEMS);
    }


    public screenWidth: any;
    public leftMenuToggle = false;
    public mainMenuToggle = false;

    protected get menus(): Observable<Menu[]> {
        this.MENUITEMS = this.http.get<Menu[]>('/api/menu').pipe(map(data => data));
        this.MENUITEMS.subscribe(next => { localStorage.menus = JSON.stringify(next); });
        return this.MENUITEMS = this.MENUITEMS.pipe(startWith(JSON.parse(localStorage.menus || '[]')));
    }

    protected get leftmenus(): Observable<Menu[]> {
        this.LEFTMENUITEMS = this.http.get<Menu[]>('/api/leftmenu').pipe(map(data => data));
        this.LEFTMENUITEMS.subscribe(next => { localStorage.leftmenus = JSON.stringify(next); });
        return this.LEFTMENUITEMS = this.LEFTMENUITEMS.pipe(startWith(JSON.parse(localStorage.leftmenus || '[]')));
    }

    // Get MENU
    public get getMenu(): Observable<Menu[]> {
        return this.menus;
    }

    // Get MENU
    public get getLeftMenu(): Observable<Menu[]> {
        return this.leftmenus;
    }

    // Windows width
    @HostListener('window:resize', ['$event'])
    onResize(event?) {
        this.screenWidth = window.innerWidth;
    }
}
