import { Component, OnInit, OnDestroy, ViewChild, TemplateRef, Input, AfterViewInit,
  Injectable, PLATFORM_ID, Inject } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { NgbModal, ModalDismissReasons, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ProductService } from '../../../services/product.service';
import { Product } from '../../../classes/product';
import {OrderService} from '../../../services/order.service';
import {toList} from '../../../helpers/matrixtolist';

@Component({
  selector: 'app-cart-modal',
  templateUrl: './cart-modal.component.html',
  styleUrls: ['./cart-modal.component.scss']
})
export class CartModalComponent implements OnInit, AfterViewInit, OnDestroy {
  public orders;
  @Input() product: Product;
  @Input() currency: any;

  @ViewChild('cartModal', { static: false }) CartModal: TemplateRef<any>;

  public closeResult: string;
  public modalOpen = false;
  public crossSellProducts: any[] = [];

  constructor(@Inject(PLATFORM_ID) private platformId: Object,
              private modalService: NgbModal,
              private productService: ProductService,
              private orderService: OrderService) {
  }

  ngOnInit(): void {
  }

  ngAfterViewInit(): void {
  }

  async openModal(product) { // the use of@ViewChild() on another compnt exposes cart modal component hence use of openModal
      await this.orderService.getOrders.subscribe(response => this.orders = response);

      // cross sell code
      this.crossSellProducts = await this.orders.map(items => items.product);

      this.crossSellProducts = toList(this.crossSellProducts, 'category', product.category).filter(
          _product => _product.id != product.id
      );

      this.crossSellProducts = Object.values(this.crossSellProducts.reduce((previousProduct, currentProduct) => {
          return Object.assign(previousProduct, {[currentProduct.id]: currentProduct});
      }, {}));
      // cross sell code

      const status = await this.productService.addToCart(product);

      if (status) {
          this.modalOpen = true;
          if (isPlatformBrowser(this.platformId)) { // For SSR
              this.modalService.open(this.CartModal, {
                  size: 'lg',
                  ariaLabelledBy: 'Cart-Modal',
                  centered: true,
                  windowClass: 'theme-modal cart-modal CartModal'
              }).result.then((result) => {
                  `Result ${result}`;
              }, (reason) => {
                  this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
              });
          }
      }
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  ngOnDestroy() {
    if (this.modalOpen){
      this.modalService.dismissAll();
    }
  }

}
