import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import {Order, Orders} from '../classes/order';
import {Product} from '../classes/product';
import {map, startWith} from 'rxjs/operators';
import {HttpClient} from '@angular/common/http';

const state = {
  checkoutItems: JSON.parse(localStorage.checkoutItems || '[]')
};

@Injectable({
  providedIn: 'root'
})
export class OrderService {
  item: Order;
  public Orders;

  constructor(private router: Router, private http: HttpClient) { }

  // Get Checkout Items
  public get checkoutItems(): Observable<any> {
    const itemsStream = new Observable(observer => {
      observer.next(state.checkoutItems);
      observer.complete();
    });
    return  itemsStream as Observable<any>;
  }

  // Create order
  public createOrder(order: Order) {
    this.item = order;

    state.checkoutItems = this.item;
    localStorage.setItem('checkoutItems', JSON.stringify(this.item));
    localStorage.removeItem('cartItems');
    this.router.navigate(['/shop/checkout/success', this.item.orderId]);
  }



    protected get orders(): Observable<Order[]> {
        this.Orders = this.http.get<Orders>('/api/orders/getOrders').pipe(map(data => data.orders));
        this.Orders.subscribe(next => { localStorage.orders = JSON.stringify(next); });
        return this.Orders = this.Orders.pipe(startWith(JSON.parse(localStorage.orders || '[]')));
    }


    public get getOrders(): Observable<Order[]> {
        return this.orders;
    }

}
