import {Order} from '../classes/order';

export function toList(matrix: any, property: string, value: any) {
    const filtered = [];
    for (let i = 0; i < matrix.length; i++) {

        const obj = matrix[i];

        for (const key in obj) {
            if (typeof(obj[key] == 'object')) {
                const item = obj[key];
                if (item[property] == value) {
                    filtered.push(item);
                }
            }
        }

    }
    return filtered;
}